.page_content{
      margin-top: 7rem;
}

.page_header{
      display: flex;
      flex-direction: column;
      align-items: center;
}

.page_header h2{
      text-transform: uppercase;
      color: var(--dark-200);
      font-size: 4rem;
}

.page_navigator{
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 2rem;
      margin-top: 1rem;
}

.page_navigator__btn{
      width: 240px;
      height: 46px;
      text-transform: uppercase;
      color: var(--light);
      background: var(--gradient-45);
      border-radius: .4rem;
      font-size: 1.4rem;
}

.page_navigator__btn.active{
      background: var(--dark-200);
}

.page_main{
      margin-top: 4rem;
      width: 100%;
}

.wrapper_gifs{
      width: 100%;
      height: auto;
      display: flex;
      gap: 1rem;
      justify-content: space-around;
      flex-wrap: wrap;
}