.banner_content{
      width: 100%;
      height: 400px;
}

.wrapper_banner{
      width: 100%;
      height: 320px;
      position: relative;
      overflow: hidden;
}

.banner_img{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--gradient-45);
}

.banner_img img{
      width: auto;
      min-height: 100%;
      height: auto;
      min-height: 100%;
      filter: grayscale(1);
      mix-blend-mode: multiply;
}

.wrapper_online{
      position: absolute;
      top: 4rem;
      right: 4rem;
      display: flex;
      gap: .5rem;
}

.online_avatar,
.online_avatar img{
      width: 38px;
      height: 38px;
      display: flex;
      border-radius: 50%;
}

.online_avatar{
      position: relative;
}

.online_dots{
      width: 14px;
      height: 14px;
      position: absolute;
      border-radius: 50%;
      background: var(--light);
      top: 0;
      right: 0;
}

.online_dots.green{
      background: var(--green);
}

.online_dots.red{
      background: var(--red-100);
}

.wrapper_online span{
      padding: .5rem 1.5rem;
      background: var(--dark-200);
      text-transform: uppercase;
      border-radius: 999px;
      height: fit-content;
}

.container.banner{
      position: relative;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
}

.container.banner h1{
      text-transform: uppercase;
      font-size: 5rem;
      line-height: 4rem;
      margin-top: 8rem;
}

.container.banner p{
      max-width: 75%;
      height: auto;
      text-align: center;
      font-size: 1.6rem;
      margin-top: 1rem;
}

.banner_header{
      width: 100%;
      height: 80px;
      background: var(--light);
      box-shadow: 0 1px 1px rgba(0,0,0,.02);
}

.container.banner_header{
      height: 100%;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: flex-end;
}

.header_user{
      height: 181px;
      width: auto;
      position: absolute;
      display: flex;
      align-items: center;
      gap: 1rem;
      top: calc(50% - 90.5px);
      left: calc(50% - 88px);
}

.user_avatar{
      width: 181px;
      height: 176px;
      display: flex;
      align-items: center;
      justify-content: center;
      background: var(--grys-100);
      clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
}

.user_avatar__img,
.user_avatar__img img{
      width: 169px;
      height: 164px;
      display: flex;
      clip-path: polygon(50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20%);
}

.header_user__data{
      height: 79px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 1rem 0;
}

.header_user__data strong{
      font-size: 1.6rem;
      text-transform: uppercase;
      line-height: 1.6rem;
}

.header_user__data span{
      color: var(--theme);
      font-size: 1.2rem;
}

.header_user__data p{
      color: var(--dark-200);
      font-weight: 600;
      letter-spacing: .1rem;
      font-size: 1.2rem;
}

.btn_xat{
      padding: 1rem 2rem;
      text-transform: uppercase;
      color: var(--light);
      font-weight: 600;
      font-size: 1.4rem;
      border-radius: .4rem;
      background: var(--gradient-45);
}